<template>
<div>
  <div class="title">热销推荐</div>
  <ul>
      <router-link
      class="item border-one"
      v-for="item of list"
      :key="item.id"
      tag="li"
      :to="'/detail/' + item.id"
      >
          <div>
              <img class="item-img" :src="item.imgUrl">
          </div>
          <div class="item-info">
              <p class="item-title">{{item.title}}</p>
              <p class="item-desc">{{item.desc}}</p>
              <button class="item-button">查看详情</button>
          </div>
      </router-link>
  </ul>
</div>
</template>

<script>
export default {
  name: 'HomeRecommend',
  props: {
    list: Array
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/mixins.styl'
  @import '~styles/variables.styl'
  .border-one
    &::after
      background-color: #ff9300
  .title
    margin-top: .2rem
    line-height: .8rem
    background-color: #eee
    text-indent: .2rem
  .item
    overflow: hidden
    display:flex
    height: 1.9rem
    position: relative
    .item-img
      width: 1.7rem
      height: 1.7rem
      padding: .1rem
    .item-info
      flex: 1
      padding: .1rem
      min-width: 0
      .item-title
        line-height: .54rem
        font-size: .32rem
        ellipsis()
      .item-desc
        line-height: .4rem
        color: $darkTextColor
        ellipsis()
      .item-button
        line-height: .44rem
        margin-top: .16rem
        background-color: #ff9300
        padding: 0 .2rem
        border: none
        border-radius: .06rem
        color: #fff
</style>



