<template>
  <div class="container" @click="handleGallaryClick">
    <div class="wrapper">
        <swiper :options="swiperOptions">
            <swiper-slide v-for="(item, index) of imgs" :key="index">
                <img class="swiper-img" :src="item">
            </swiper-slide>
            <div class="swiper-pagination swiper-pagination-active" slot="pagination"></div>
        </swiper>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommonGallary',
  props: {
    imgs: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      swiperOptions: {
        pagination: {
          el: '.swiper-pagination',
          type: 'fraction'
        },
        observeParents: true,
        observer: true
      }
    }
  },
  methods: {
    handleGallaryClick () {
      this.$emit('handleGallaryClick')
    }
  }
}
</script>

<style lang="stylus" scoped>
  .container >>> .swiper-container
    overflow: inherit
  .container
    display: flex
    flex-direction: column
    justify-content: center
    z-index: 99
    position: fixed
    left: 0
    right: 0
    top: 0
    bottom: 0
    background: #000
    .wrapper
      background: #fff
      .swiper-img
        width: 100%
      .swiper-pagination
        color: #ffffff
        bottom: -1rem
</style>



